@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@800&display=swap");

/* #030d42 */
/* #003474 */
/* #151515 */
/* #040f43 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: 0;
  outline: 0;
  text-decoration: none;
  list-style: none;

  /* Making Color White of Every Text */
  color: #fff;

  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

:root {
  --color-bg: #030d42;
  --color-bg-secondary: #040f43;
  --color-bg-light: #003474;
  --color-bg-navbar: #151515;
}
/* Targeting All The Images */
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.flex {
  display: flex;
}

.items-start {
  align-items: start;
}
.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}
.flex-col {
  flex-direction: column;
}
.navbar {
  background: var(--color-bg-navbar);
  height: 70px;
  width: 100%;
  padding: 10px 50px;
  color: white;
}

.nav-toggle {
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;
  height: 40px;
  width: 40px;
  padding: 10px;
  border-radius: 50%;
  display: none;
  cursor: pointer;
}

.nav-toggle:hover {
  background: rgba(255, 255, 255, 0.1);
}

.nav-toggle .cross {
  margin-top: -8px;
}

.nav-toggle .bar {
  height: 2px;
  width: 100%;
  background: #fff;
}

.logo {
  height: 80px;
  width: 150px;
  object-fit: cover;
  overflow: hidden;
}
.nav-links {
  gap: 2rem;
  font-size: 16px;
  font-weight: 200;
  text-transform: uppercase;
}

.section-1 {
  background-image: url("./assets/section\ 1\ bg.png");
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.nav-links-mobile {
  display: none;
  align-items: center;
  flex-direction: column;
  background: var(--color-bg-light);
  position: absolute;
  border-radius: 20px;
  transform-origin: top;
  transition: all 0.3s ease;
  transform: scale(1, 0);
  overflow: hidden;
  top: 80px;
  right: 10px;
}

.nav-links-mobile.active {
  transform: scale(1, 1);
}
.nav-links-mobile li {
  width: 100%;
  padding: 20px;
  transition: all 0.3s ease;
}
.nav-links-mobile li:hover {
  background: var(--color-bg-secondary);
}
.section-1 .main .main-left {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: start;
}

.section-1 .main .main-right {
  height: 50vh;
  width: 70%;
  display: flex;
  align-items: start;
  justify-content: center;
  margin-left: 100px;
  text-align: start;
  flex-direction: column;
}

h1 {
  font-family: "Nunito", sans-serif;
  font-size: 35px;
}
.btn {
  background: none;
  border: 0;
  padding: 10px 20px;
}
.main-right .btn-section-1 {
  background: #043f87;
  border-radius: 20px;
  font-weight: bold;
  margin: 40px 0 0 70px;
  text-transform: uppercase;
}
.section-2 {
  background: var(--color-bg-light);
  padding: 40px;
}

.section-2 .content {
  border: 2px dotted gray;
  border-style: dashed;
  padding: 40px;
  font-size: 16px;
}

.section-2 .content .left {
  width: 70%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-start;
}

.section-2 .content .left .btn {
  background: #ffff;
  color: #000;
  text-transform: uppercase;
}
.section-2 .content .right {
  background: #fff;
  border-radius: 20px;
  height: 300px;
  width: 300px;
}
.section-4 {
  background: var(--color-bg-light);
  padding: 40px;
}

.section-4 .content {
  border: 2px dotted gray;
  border-style: dashed;
  padding: 40px;
  font-size: 16px;
}

.section-4 .content .left {
  width: 65%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-start;
  font-size: 20px;
  justify-content: space-evenly;
  align-items: center;
}

.section-4 .content .right {
  width: 100%;
  height: 350px;
}
.section-3 {
  background: var(--color-bg-secondary);
  width: 100%;
  padding: 40px;
}
.section-3-header {
  gap: 2rem;
}
.section-3-header p {
  text-transform: uppercase;
}
.section-3-header h1 {
  font-size: 30px;
  word-spacing: 4px;
}
.project-cards {
  flex-wrap: wrap;
  gap: 1rem;
}

.project-card {
  height: 500px;
  width: 350px;
  overflow: hidden;
  padding: 60px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}
.project-card img {
  height: 200px;
  width: 200px;
  border-radius: 10px;
  object-fit: cover;
}

.project-card div {
  margin-left: 130px;
}
.section-5 {
  background-color: var(--color-bg);
  padding: 40px;
}

.section-5 h1 {
  font-size: 30px;
}
.companies-cards {
  gap: 3rem;
  padding: 70px 40px;
  flex-wrap: wrap;
}
.companies-card {
  height: 100px;
  width: 150px;
  border-radius: 10px;
  background: white;
}

.section-6 {
  background: var(--color-bg-light);
  padding: 20px;
}

.section-6 .content {
  border: 2px dotted gray;
  border-style: dashed;
  padding: 40px;
  font-size: 16px;
}

.section-6 .content .left {
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-start;
}

.section-6 .content .left .btn {
  background: #ffff;
  color: #000;
  text-transform: uppercase;
}
.section-6.content .right {
  background: #fff;
  border-radius: 20px;
  height: 300px;
  width: 300px;
}
@media screen and (max-width: 1240px) {
  .navbar {
    padding: 10px;
  }
  .nav-links {
    display: none;
  }
  .nav-toggle {
    display: flex;
  }
  .nav-links-mobile {
    display: block;
  }
}

@media screen and (max-width: 840px) {
  .section-1 .main {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .section-1 .main .main-left {
    height: 400px;
    width: 100vw;
  }
  .section-1 .main-right .btn-section-1 {
    margin: 10px auto;
  }
  .section-1 .main-right {
    width: 100vw !important;
    margin-left: 0 !important;
    font-size: large;
    height: max-content !important;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
    align-items: center;
    justify-content: center;
  }
  .section-1 .main-right h1 {
    font-size: 30px;
    text-align: center;
    width: 100%;
    margin: 10px 0;
  }

  .section-2,
  .section-4 {
    padding: 10px;
  }
  .section-2 .content,
  .section-4 .content {
    justify-content: center;
    gap: 1rem;
    align-items: center;
    padding: 10px;
    width: 100%;
    flex-direction: column;
  }
  .section-4 .content img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .section-2 .content .left,
  .section-4 .content .left {
    width: 100%;
    padding: 20px;
  }
}
@media screen and (max-width: 780px) {
  .section-6 {
    padding: 10px;
  }
  .section-6 .content {
    justify-content: center;
    gap: 1rem;
    align-items: center;
    padding: 10px;
    width: 100%;
    flex-direction: column;
  }
  .section-6 .content .left {
    width: 100%;
    padding: 20px;
  }

  .section-5 {
    background-color: var(--color-bg);
    padding: 20px 0;
    text-align: center;
  }

  .companies-cards {
    gap: 1rem;
    padding: 50px 0px;
    flex-wrap: wrap;
  }
  .companies-card {
    height: 80px;
    /* margin: 0 10px; */
    width: 150px;
    border-radius: 10px;
    background: white;
  }
}
